import GabaritObject from "jx/editor/editionitems/GabaritObject";
export default class QuizManagerItemold extends GabaritObject {
    constructor(dataObject) {
        dataObject.gabarit = QuizManagerItem.gabaritTemplate;
        dataObject.data.preview = true;
        super(dataObject);
        console.log("quizmanager", this);
        this.gabarit = [{ id: `Configurer`, type: 'editionitems.ButtonItem', onclick: () => this.configure(), description: '', confirmBeforeLaunch: false }];
        this.factory.addItems(this.gabarit, (this.dataObject.tabs ? undefined : this.view));
    }
    static get gabaritTemplate() {
        return [];
    }
    configure() {
        this.jx.editor.popup({
            title: "Launch Script",
            content: "<h1>Configuration</h1>",
            buttons: {
                ok: { label: "Lancer" },
                close: { label: "Fermer" }
            },
            onclose: evt => {
                console.log(evt);
                if (evt.action == "ok") {
                    console.log('go');
                    this.gabarit.push({
                        "type": "editionitems.Text",
                        "id": "resourceID",
                        "value": "COMMON_LIB",
                        "editable": true,
                        "visible": true
                    });
                    this.refreshLocalView();
                    //this.factory.addItems(this.gabarit, (this.dataObject.tabs ? undefined : this.view));
                }
            }
        });
    }
}
